
class LoginForm extends React.Component {
    constructor (props) {
        super(props)
        this.state = {
            username: "",
            password: "",
            role: ""
        }
    }
    render() {
        return (
            <div className="wrapper">
            <form className="form-signin" onSubmit={this.onSubmit}>       
              <h2 className="form-signin-heading">Vui lòng đăng nhập</h2>
              <input type="text" className="form-control" name="username" placeholder="Tên đăng nhập" required="" autofocus="" value={this.state.username}/>
              <input type="password" className="form-control" name="password" placeholder="Mật khẩu" required=""  value={this.state.password}/>   
              <select className="form-control select" name="role" value={this.state.role}>
                <option value="STU">Sinh viên</option>
                <option value="LEC">Gỉang viên</option>
                <option value="MAN">Quản lý</option>
              </select>   
              <button className="btn btn-lg btn-primary btn-block" type="submit">Đăng nhập</button>   
            </form>
          </div>
        );
    }

    onSubmit = () => {

    }
}

export default LoginForm;